import Vue from "vue";
import VueRouter from "vue-router";

import Landing from "../views/Landing.vue";
import Dashboard from "../views/Dashboard.vue";

import Learn from "../views/Learn.vue";
import Practice from "../views/Practice.vue";
import Collab from "../views/Collab.vue";
import Contribute from "../views/Contribute.vue";

import Enrolled from "../components/learn/courselist/Enrolled.vue";
import Live from "../components/learn/courselist/Live.vue";
import Regular from "../components/learn/courselist/Regular.vue";
import Micro from "../components/learn/courselist/Micro.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Landing",
    component: Landing
  },
  {
    path: "/dashboard",
    name: "Dashboard",
    component: Dashboard
  },
  {
    path: "/learn",
    redirect: "/learn/enrolled",
    name: "Learn",
    component: Learn,
    children: [
      {
        path: "live",
        component: Live
      },
      {
        path: "regular",
        component: Regular
      },
      {
        path: "micro",
        component: Micro
      },
      {
        path: "enrolled",
        component: Enrolled
      },
      {
        path: "",
        redirect: "enrolled"
      }
    ]
  },
  {
    path: "/practice",
    name: "Practice",
    component: Practice
  },
  {
    path: "/collab",
    name: "Collab",
    component: Collab
  },
  {
    path: "/contribute",
    name: "Contribute",
    component: Contribute
  }

  // { // follow this later on to improve performance.
  //   path: "/dashboard",
  //   name: "Dashboard",
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () =>
  //     import(/* webpackChunkName: "about" */ "../views/About.vue")
  // }
];

const router = new VueRouter({
  routes
});

export default router;
